.HeroItem {
    position: relative;
    height: 400px;
    background-image: var(--image-url);
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center center;
    overflow: hidden;
}

.HeroItem .info {
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    padding: 10px;
    opacity: 0.25;
    transition: all 0.5s ease;
    background: rgba(0, 0, 0, 0.8);
    color: white;
}

.HeroItem:hover .info {
    opacity: 1;
    top: 50%;
    transform: translateY(-50%);
}

.HeroItem h2 {
    margin: 0;
    font-size: 3rem;
    font-weight: bold;
}

.HeroItem button {
    font-size: 1.5rem;
    vertical-align: middle;
    margin-left: 0.5rem;
}

.HeroItem .comics {
    list-style-type: circle;
}

.HeroItem .comics li {
    display: inline-block;
    margin: 5px;
}

.HeroItem .comics li::before {
    content: "‚óè\00a0";
}

.HeroItem .comics a {
    color: white;
}
.HeroItem .comics a:hover {
    color: yellow;
}
